<template>
  <div>
    <a-row type="flex" justify="center" align="top">
      <a-col :span="12">
        <div>
          <a-space style="margin-top: 0.2rem;margin-left:0.3rem;float: left">
            <b>:</b>
            <a-cascader
              v-model="selectedValues"
              :fieldNames="{ label: 'name', value: 'code', children: 'children' }"
              :options="areaData"
              placeholder=""
              :size="size"
              @change="onChange"
            />
          </a-space>
          <a-space style="margin-top: 0.2rem;margin-left:0.3rem;float: left">
            <b>:</b>
            <a-date-picker v-model="birDate" :default-value="moment('2000/01/01', dateFormat)" :format="dateFormat" :size="size" style="width: 8rem" />
            <b>:</b>
            <a-input-number id="inputNumber" v-model="value" :size="size" :min="1" :max="100" />
          </a-space>
          <a-space style="margin-top: 0.2rem;margin-left:0.3rem;float: left">
            <b>:</b>
            <a-input v-model="hl" placeholder="" :size="size"/>
            <a-button type="danger" :size="size" @click="random">
              
            </a-button>
            <a-button type="primary" :size="size" @click="onCopy">
              
            </a-button>
          </a-space>

        </div>
      </a-col>
      <a-col :span="12">
        <div class="height-1000">
          <a-textarea v-model="outText" placeholder="!!!" :rows="18"/>
        </div>
      </a-col>
    </a-row>

  </div>
</template>

<script>
import areaData from "@/views/tools/randDataTool/com/citys";
import randomTools from '@/views/tools/randDataTool/com/randomTools'
import moment from 'moment';

export default {
  mixins: [randomTools],
  data() {
    return {
      moment,
      dateFormat:'YYYYMMDD',
      //
      areaData,
      //
      selectedValues: [],
      outText: '',
      value: 1,
      size: 'small',
      bankId: '104',
      hl: '',
      area:'',
      birDate: '20200101'
    };
  },
  watch: {
    defaultValue(newValue) {
      if (newValue.length) {
        this.selectedValues = newValue;
      } else {
        this.selectedValues = [];
      }
    },
  },
  methods: {
    onChange(value) {
      console.log('changed', value);
      this.selectedValues = value
      this.area = value[2]
    }
    ,
    handleChange(value) {
      console.log(`selected ${value}`);
      this.bankId = value
    }
    ,
    random() {
      this.outText = ''
      let value = this.value;
      for (value; value > 0; value--) {
        this.outText += randomTools.getIdCard(this.area,this.birDate) + this.hl + '\n'
      }
    }
    ,
    onCopy() {
      this.$copyText(this.outText).then(
          e => {
            console.log('', e);
            this.$message.success("")
            this.change()
          },
          e => {
            console.log('', e);
            this.$message.error("")
            this.change()
          }
      )
    }
    ,
  }
  ,
};
</script>

