<template>
  <div>
    <a-row type="flex" justify="center" align="top">
      <a-col :span="12">
        <div>
          <a-space>
            <b>:</b>
            <a-input-number id="inputNumber" v-model="value" :size="size" :min="1" :max="100" @change="onChange"/>
            <b>:</b>
            <a-input v-model="hl" placeholder="" :size="size" style="width: 10rem"/>
          </a-space>
          <a-space style="margin-top: 0.2rem;margin-left:0.3rem;" >
            <a-button type="danger" :size="size" @click="random">
              
            </a-button>
            <a-button type="primary" :size="size" @click="onCopy">
              
            </a-button>
          </a-space>

        </div>
      </a-col>
      <a-col :span="12">
        <div class="height-1000">
          <a-textarea v-model="outText" placeholder="!!!" :rows="18"/>
        </div>
      </a-col>
    </a-row>

  </div>
</template>

<script>
import randomTools from '@/views/tools/randDataTool/com/randomTools'

export default {
  mixins:[randomTools],
  data() {
    return {
      outText: '',
      value: 1,
      size: 'small',
      hl: ''
    };
  },
  methods: {
    onChange(value) {
      console.log('changed', value);
      this.value = value
    },
    handleChange(value) {
      console.log(`selected ${value}`);
      this.bankId = value
    },
    random(){
      this.outText = ''
      let value = this.value;
      for(value;value > 0;value --){
        this.outText += randomTools.getMoble() + this.hl + '\n'
      }
    },
    onCopy() {
      this.$copyText(this.outText).then(
          e=>{
            console.log('', e);
            this.$message.success("")
            this.change()
          },
          e=>{
            console.log('', e);
            this.$message.error("")
            this.change()
          }
      )
    },
  },
};
</script>

